.swiper {
    cursor: grab;
}

.swiper-head {
    width: 100%;
    height: 800px;
    .swiper-slide {
        text-align: center;
        font-size: 18px;
        background: #fff;
        position: relative;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .slide-head {
        background-repeat: no-repeat;
        background-position: center center;
        background-size: cover;
        &::before {
            content: "";
            position: absolute;
            width: 100%;
            height: 100%;
            background-color: rgba(0, 0, 0, 0.6);
            top: 0;
        }
        .title-bold {
            max-width: 40%;
            text-align: left;
            color: white;
            position: absolute;
            padding-left: 20px;
            margin-top: 80px;
        }
    }
    .swiper-pagination {
        text-align: left;
        padding-left: 4%;
        margin-bottom: 24px;
        color: #fff;
    }
}

.title-slide {
    font-size: 42px;
    font-weight: bold;
    color: white;
    a {
        color: #000;
    }
}

.desc-slide {
    font-size: 16px;
    line-height: 1.5;
    padding-top: 10px;
}

.swiper-middle {
    width: 100%;
    min-height: 330px;
    padding: 24px 0px;
    .swiper-button-next,
    .swiper-button-prev {
        cursor: pointer;
        left: 0;
        right: 0;
        margin: 0;
        &::after {
            font-size: 16px;
            font-weight: bold;
        }
    }
    .sw-slide-inner {
        display: grid;
        grid-template-columns: repeat(auto-fill, minmax(50%, 1fr));
        &-img {
            display: flex;
            align-items: center;
            justify-content: flex-end;
            img {
                max-width: 350px;
                max-height: auto;
            }
        }
        &-text {
            padding: 40px 0;
            .desc-slide {
                padding-top: 24px;
                color: #464242;
            }
        }
    }
    .title-bold {
        font-size: 24px;
    }
    .swiper-pagination {
        text-align: left;
        color: #000;
        padding-left: 10px;
        transform: translateY(-25px);
    }
}

.monument {
    width: 400px;
    height: 440px;
    cursor: default;
    .swiper-slide {
        width: 400px;
        height: 400px;
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            object-position: center center;
        }
    }
    .swiper-button-next,
    .swiper-rtl,
    .swiper-button-prev {
        top: inherit;
        bottom: 14px;
        cursor: pointer;
        width: 12px;
        height: 12px;
        &::after {
            font-size: 18px;
            color: #000;
        }
    }
    .swiper-button-next {
        right: 67%;
    }
    .swiper-pagination-fraction {
        width: auto;
        margin-left: 13%;
    }
}

.info-slide {
    margin-top: 15px;
    width: 100%;
    height: 315px;
    cursor: inherit;
    .swiper-slide {
        width: 100%;
        max-height: 260px;
        img {
            display: block;
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
    .swiper-button-next,
    .swiper-rtl .swiper-button-prev {
        right: 5px;
        left: 30px;
    }
    .swiper-button-next,
    .swiper-button-prev {
        top: inherit;
        bottom: 10px;
        color: black;
        &::after {
            font-size: 22px;
        }
    }
}
