@media screen and (min-width: 992px) {
    .feature_content,
    .basic_content {
        width: 80%;
        margin: 0 auto;
    }
}

@media screen and (max-width: 991px) {
    .swiper-head .slide-head .title-bold {
        max-width: 80%;
    }
    .swiper-middle {
        .sw-slide-inner {
            grid-template-columns: repeat(auto-fill, minmax(100%, 1fr));
            &-img {
                justify-content: center;
            }
        }
    }
    .news-view {
        grid-template-columns: repeat(auto-fill, minmax(98%, 1fr));
        &-img {
            flex-direction: inherit;
            flex-wrap: wrap;
            align-items: start;
            justify-content: start;
            img {
                margin: 16px;
            }
        }
    }
    .head-content {
        max-width: 100%;
        flex-direction: column-reverse;
        div {
            &:first-child {
                max-width: 95%;
                padding-bottom: 10%;
            }
        }
        .ekspo-info {
            display: none;
        }
    }
    .structures__item__link a {
        width: 33.33%;
    }
    .info-slide {
        height: 230px;
        .swiper-slide {
            max-height: 180px;
        }
    }

    .header {
        .nav-content {
            width: 54%;
        }
    }

    .block__grid__item .img-cover {
        height: 210px;
    }
}

@media screen and (min-width: 830px) {
    .block__monument {
        padding: 0 80px;
    }
}

@media screen and (max-width: 767px) {
    .title-slide {
        font-size: 28px;
    }
    .desc-slide {
        font-size: 14px;
    }
    .swiper-head {
        height: 400px;
        .slide-head {
            .title-bold {
                max-width: 90%;
                padding: 0;
                padding-bottom: 10px;
                margin-top: 0;
            }
            .container {
                margin-bottom: 25px;
            }
        }
    }
    .news-content {
        grid-template-columns: repeat(auto-fill, minmax(100%, 1fr));
    }
    .news-item {
        max-width: 80%;
    }
    .bg-change,
    .head-content {
        height: 400px;
    }
    .other-content {
        grid-template-columns: 1fr;
    }
    .gallery-photos {
        grid-template-columns: repeat(auto-fill, minmax(25%, 1fr));
        gap: 10px;
    }
    .info__block {
        grid-template-columns: 1fr;
        &.gap {
            grid-gap: 20px;
        }
        &.reverse {
            display: flex;
            flex-direction: column-reverse;
            .img-mob {
                display: block;
            }
            .info__block__right {
                justify-content: start;
                align-items: start;
            }
        }
    }
    .info-slide {
        height: 270px;
        .swiper-slide {
            max-height: 215px;
        }
    }
    .md-block {
        max-width: 70%;
    }
    .lang {
        display: none;
    }
    .ticket {
        margin-left: 30px;
    }
    .block__grid {
        gap: 20px;
    }
    .desc-monument p {
        overflow: hidden;
        height: 40px;
    }
    .basic {
        height: auto;
    }
    .header {
        .nav-content {
            width: auto;
        }
    }
}

@media screen and (max-width: 575px) {
    .header .nav .logo-text {
        display: none;
    }
    .desc-slide {
        font-size: 14px;
    }
    .title-slide {
        font-size: 24px;
    }
    .category-title {
        font-size: 24px;
        &::after {
            content: "";
            width: calc(220px);
            height: 2px;
            background-color: #000;
            position: absolute;
            bottom: 0;
            left: 0;
        }
    }
    .basic_content {
        grid-template-columns: repeat(auto-fill, minmax(35%, 1fr));
    }
    .bn-title {
        font-size: 22px;
    }
    .feature_content {
        grid-template-columns: repeat(auto-fill, minmax(45%, 1fr));
    }
    .news-item {
        max-width: 100%;
    }
    .news-view {
        text-align: left;
    }
    .news-view-img {
        img {
            margin: 12px 0px;
            max-width: 100%;
        }
    }
    .n-title {
        font-size: 22px;
    }
    .head-content {
        max-width: 95%;
    }
    .gallery-photos {
        grid-template-columns: repeat(auto-fill, minmax(48%, 1fr));
        gap: 10px;
        padding: 10px 2px;
    }
    .st-title {
        font-size: 18px;
    }
    .structures__item__link a {
        width: 50%;
    }
    .info-slide {
        height: 240px;
        .swiper-slide {
            max-height: 185px;
        }
    }
    .mini-title {
        font-size: 18px;
    }
    .block__grid {
        grid-template-columns: repeat(auto-fill, minmax(100%, 1fr));
        &__item {
            .img-cover {
                height: 100%;
            }
        }
    }
    .desc-monument {
        p {
            font-size: 16px;
        }
    }
    .text-gallery {
        font-size: 14px;
    }
}

@media screen and (max-width: 460px) {
    .monument {
        width: 280px;
        height: 320px;
        .swiper-slide {
            width: 100%;
            height: 280px;
        }
        .swiper-button-next {
            right: 60%;
        }
        .swiper-pagination-fraction {
            margin-left: 15%;
        }
    }
    .info-slide {
        height: 182px;
        .swiper-slide {
            max-height: 132px;
        }
    }
    .payment__form {
        input,
        select,
        textarea {
            width: 100%;
        }
    }
    .md-block {
        max-width: 95%;
    }
    .block__grid__wrapper {
        width: 100%;
    }
    .by-ticket_btn {
        flex-direction: column;
        justify-content: start;
        align-items: start;
    }

    .res-contact {
        input {
            height: 20px;
        }
    }
}

@media screen and (max-width: 360px) {
    .feature_content {
        grid-template-columns: repeat(auto-fill, minmax(100%, 1fr));
    }
    .swiper-middle .sw-slide-inner-img img {
        max-width: 100%;
        max-height: 100%;
    }
    .header .nav .lang {
        margin-right: 15px;
    }
    .info-slide {
        height: 170px;
        .swiper-slide {
            max-height: 115px;
        }
    }
}
