@import "../../node_modules/swiper/swiper-bundle.min";
@import "./font/gilroy";

*,
*::before,
*::after {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: Gilroy, sans-serif;
}

a {
    color: inherit;
    text-decoration: none;
}

ul,
li {
    list-style: none;
    padding: 0;
    font-size: 16px;
}
img {
    vertical-align: middle;
    border-style: none;
}
.container {
    max-width: 1200px;
    width: 100%;
    margin: 0 auto;
    padding: 0px 16px;
    position: relative;
}

body {
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center right;
    background-origin: padding-box;
    min-height: 100vh;
    font-size: 16px;
}

.header {
    background-color: white;
    color: black;
    position: sticky;
    width: 100%;
    top: 0;
    z-index: 99;
    .nav {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 8px 0px;
        &-content {
            display: flex;
            align-items: center;
            justify-content: space-evenly;
        }
        &-right {
            display: flex;
            align-items: center;
        }
        .logo {
            img {
                max-width: 55px;
            }
        }
        .logo-text {
            margin-left: 20px;
            span {
                font-size: 16px;
                font-weight: 500;
            }
        }
    }
    &_content {
        display: none;
        padding: 16px 6px;
        background-color: #fff;
        color: black;
        border-top: 1px solid #d3d3d3ea;
        transition: display 0.8s ease-in-out;
        &.active {
            display: block;
            transition: display 0.8s ease-in-out;
            overflow-y: scroll;
            height: calc(100vh - 70px);
        }
    }
    &.active {
        box-shadow: 2px 4px 25px rgba($color: #000000, $alpha: 0.4);
    }
}

.hidden {
    overflow: hidden;
}

.content {
    &_menu {
        display: grid;
        grid-template-columns: repeat(auto-fill, minmax(240px, 1fr));
        gap: 25px;
        .tmenu {
            margin: 6px 0px;
            padding-left: 3px;
            li {
                word-wrap: break-word;
                margin-bottom: 4px;
                a {
                    font-size: 16px;
                    line-height: 1.5;
                    &:hover {
                        text-decoration: underline;
                    }
                }
            }
        }
        .address-title {
            margin: 6px 0px;
            display: block;
            font-size: 14px;
        }
        .menu-title {
            font-size: 18px;
            font-weight: 700;
        }
        .letter {
            p {
                font-size: 14px;
            }
            form {
                width: 100%;
                margin-top: 10px;
                position: relative;
                input {
                    border: none;
                    border-bottom: 1px solid #d3d3d3ea;
                    outline: none;
                    width: 100%;
                    height: 25px;
                    padding-left: 4px;
                }
                .letter_btn {
                    border: none;
                    outline: none;
                    background-color: transparent;
                    cursor: pointer;
                    width: 10px;
                    height: 10px;
                    background-image: url("@img/icons/right.png");
                    background-repeat: no-repeat;
                    background-size: contain;
                    background-position: 0 0;
                    position: absolute;
                    right: 0;
                    transform: translateY(50%);
                }
            }
        }
    }
    &__soc {
        display: flex;
        flex-direction: column;
        justify-content: start;
        a {
            display: flex;
            align-items: center;
            margin: 4px 0px;
            b {
                padding-left: 4px;
            }
            &:hover {
                text-decoration: underline;
            }
        }
    }
}

.hamburger {
    position: relative;
    width: 30px;
    height: 18px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    cursor: pointer;
    transition: all ease 0.6s;
    &__item {
        display: block;
        width: 30px;
        height: 2px;
        background-color: black;
        transition: all 0.3s ease-in-out;
    }
    &.active {
        transition: transform 0.4s ease;
        .hamburger__item:nth-child(2) {
            opacity: 0;
        }

        .hamburger__item:nth-child(1) {
            transform: translateY(8px) rotate(45deg);
        }

        .hamburger__item:nth-child(3) {
            transform: translateY(-8px) rotate(-45deg);
        }
    }
}

footer {
    padding: 24px 0;
    border-top: 1px solid #d3d3d3ea;
    .footer_bottom {
        padding: 6px 0px;
        border-top: 1px solid #d3d3d3ea;
        margin-top: 4px;
        display: flex;
        align-items: center;
        justify-content: space-around;
    }
}

.category-title {
    font-size: 32px;
    font-weight: bold;
    position: relative;
    line-height: 1.5;
}

.news {
    padding: 60px 0;
}

.ekspo {
    padding: 60px 0;
    display: flex;
    align-items: center;
}

.news-content {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(48%, 2fr));
    gap: 20px;
    padding: 12px 0px;
    &__item {
        position: relative;
        &__text {
            display: flex;
            align-items: center;
            justify-content: space-evenly;
            flex-direction: column;
            color: white;
            text-align: center;
            position: absolute;
            left: 0;
            right: 0;
            bottom: 0;
            top: 0;
            padding: 8px;
            opacity: 0;
            transition: opacity 0.5s ease-in-out;

            p {
                position: relative;
            }

            a {
                position: relative;
                &::after {
                    content: "\2192";
                    position: absolute;
                    right: -17px;
                }
                &:hover {
                    text-decoration: underline;
                }
            }

            &:hover {
                opacity: 1;
            }

            &::before {
                content: "";
                position: absolute;
                width: 100%;
                height: 100%;
                background-color: rgba(0, 0, 0, 0.7);
            }
        }
    }
    &-img {
        max-width: 100%;
        max-height: 100%;
        object-fit: cover;
        width: 100%;
        height: 100%;
    }
    &-left {
        font-size: 18px;
    }
}
.news-item {
    position: relative;
    padding-left: 15px;
    line-height: 1.5;
    margin: 6px 0px;
    max-width: 70%;
    cursor: pointer;
    &.active {
        font-weight: 600;
    }
    &::before {
        content: "";
        width: 10px;
        height: 2px;
        position: absolute;
        left: 0;
        top: 50%;
        background-color: #000;
    }
}
.news-title {
    font-size: 24px;
    font-weight: bold;
    margin-bottom: 0.5rem;
    a:hover {
        text-decoration: underline;
    }
}
.news-item-content {
    display: none;
    &.active {
        display: block;
    }
}

.ticket {
    display: none;
    margin-right: 30px;
    width: 107px;
    &.active {
        display: block;
    }
    a {
        display: flex;
        align-items: center;
        &:hover {
            text-decoration: underline;
        }
        img {
            padding-left: 4px;
        }
    }
}

.news-content__item {
    display: none;
    &.active {
        display: block;
    }
}

.feature {
    padding: 60px 0;
    display: flex;
    align-items: center;
    &_content {
        padding: 32px 0px;
        display: grid;
        grid-template-columns: repeat(auto-fill, minmax(48%, 1fr));
        gap: 20px;
    }
    &__item {
        display: flex;
        flex-direction: column;
        &__img {
            padding-bottom: 8px;
            img {
                max-width: 50px;
            }
        }
        a {
            font-size: 18px;
            &:hover {
                text-decoration: underline;
            }
        }
        .link-next {
            display: flex;
            align-items: center;
            justify-content: start;
            height: 100%;
            a {
                position: relative;
                &::after {
                    content: ">";
                    position: absolute;
                    top: 2px;
                    right: 0;
                    left: 100%;
                    width: 12px;
                    height: auto;
                    font-weight: normal;
                }
            }
        }
    }
}

.basic {
    padding: 60px 0;
    display: flex;
    align-items: center;
    &_content {
        padding: 12px 0px;
        display: grid;
        grid-template-columns: repeat(auto-fill, minmax(45%, 1fr));
        gap: 20px;
    }
}
.bn-title {
    font-size: 30px;
    font-weight: bold;
}

.btn-top {
    display: none;
    position: fixed;
    bottom: 20px;
    right: 30px;
    z-index: 100;
    background-color: #fff;
    box-shadow: 2px 4px 25px rgba($color: #000000, $alpha: 0.4);
    color: black;
    cursor: pointer;
    border-radius: 8px;
    font-size: 24px;
    font-weight: bold;
    width: 40px;
    height: 40px;
    div {
        width: 100%;
        height: 100%;
        display: flex;
        align-items: flex-start;
        justify-content: center;
        padding-top: 5px;
    }
    &:hover {
        opacity: 0.9;
    }
}

.news-view {
    display: grid;
    grid-template-columns: 60% 39%;
    gap: 20px;
    text-align: justify;
    padding-bottom: 20px;
    &-img {
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: end;
        img {
            max-width: 380px;
            max-height: 100%;
            margin: 16px 0px;
        }
    }
}
.n-title {
    font-size: 26px;
    font-weight: bold;
}
.n-text {
    font-size: 16px;
    font-weight: normal;
    padding: 6px 0px;
    line-height: 1.5;
}

.bg-change {
    width: 100%;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
    display: block;
    position: relative;
    transition: all 0.8s ease;
    &::before {
        content: "";
        background-color: #000000a6;
        position: absolute;
        top: 0;
        width: 100%;
        height: 100%;
    }
}
.head-content {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 100%;
    padding: 24px 0px;
    height: 700px;
    color: white;
    padding-top: 60px;
    div {
        &:first-child {
            max-width: 50%;
        }
    }
    .ekspo-info {
        display: flex;
        flex-direction: column;
        &__item {
            margin: 10px 0px;
            position: relative;
            padding-bottom: 5px;
            &::after {
                content: "";
                width: 140px;
                height: 1px;
                background-color: #d3d3d3ea;
                position: absolute;
                bottom: 0;
            }
        }
        a:hover {
            text-decoration: underline;
        }
    }
}

.other {
    padding: 30px 0px;
    border-top: 1px solid #d3d3d3ea;
    &-content {
        display: grid;
        grid-template-columns: 48% 48%;
        gap: 20px;
        padding: 20px 0px;
        a:hover {
            text-decoration: underline;
        }
    }
}

.gallery {
    padding: 30px 0;
    &-photos {
        display: grid;
        grid-template-columns: repeat(auto-fill, minmax(20%, 1fr));
        grid-gap: 15px;
        padding: 15px;
        &__item {
            position: relative;
        }
        .text-gallery {
            position: absolute;
            left: 0;
            bottom: 0;
            right: 0;
            background: rgba(0, 0, 0, 0.493);
            color: white;
            padding: 3px;
            opacity: 0;
            transition: opacity 0.5s ease;
        }
        a {
            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
                display: block;
            }

            &:hover {
                .text-gallery {
                    opacity: 1;
                }
            }
        }
    }
}

.img-fluid {
    max-width: 100%;
    height: auto;
}

.section__block {
    padding: 60px 6px;
    .category-title {
        text-align: center;
        &::before,
        &::after {
            display: none;
        }
    }
}
.structures {
    position: relative;
    &__item {
        position: relative;
        padding: 10px 0px;
        .st-title::before {
            content: "";
            display: block;
            width: 12px;
            height: 2px;
            background-color: #000;
            transform: translate(-15px, 12px);
        }
        &::before {
            content: "";
            position: absolute;
            width: 2px;
            height: 100%;
            background-color: #000;
            transform: translate(-15px, 12px);
        }
        &__link {
            display: flex;
            flex-wrap: wrap;
            align-items: start;
            a {
                padding: 8px;
                width: 25%;
                &:hover {
                    text-decoration: underline;
                }
            }
        }
        &:last-child {
            &::before {
                display: none;
            }
        }
    }
}
.st-title {
    font-size: 22px;
    font-weight: bold;
    a:hover {
        text-decoration: underline;
    }
}

.link {
    color: #464242;
    &::after {
        content: "\27A4";
        padding-left: 1px;
        font-size: 14px;
    }
}

.linkhref:hover {
    text-decoration: underline;
}

.info__block {
    display: grid;
    grid-template-columns: 0.4fr 1fr;
    grid-gap: 20px;
    margin: 12px 0px;
    .link {
        display: inline-block;
        margin-top: 12px;
        &:hover {
            text-decoration: underline;
        }
    }
    &.gap {
        grid-gap: 60px;
    }
    .img-mob {
        display: none;
    }
}
.my-1 {
    margin: 1em 0em;
}

.payment__form {
    color: #464242;
    display: flex;
    flex-direction: column;
    label {
        font-size: 16px;
        font-weight: 600;
    }
    input,
    textarea {
        width: 360px;
        height: 30px;
        border: none;
        border-bottom: 1px solid #a8a8a8;
        color: #a8a8a8;
        outline: none;
        padding: 3px;
    }
    select {
        width: 360px;
        height: 30px;
        color: #a8a8a8;
        outline: none;
        margin: 3px 0px;
        padding: 3px;
    }
    &__item {
        display: flex;
        flex-direction: column;
        margin: 6px 0px;
    }
    .btn-submit {
        background-color: transparent;
        color: #464242;
        font-size: 17px;
        font-weight: 600;
        display: flex;
        align-items: center;
        justify-content: space-between;
        border: none;
        width: 100px;
        cursor: pointer;
    }
}

.links {
    display: flex;
    flex-direction: column;
    padding-left: 6px;
    a {
        margin: 3px 0px;
        width: fit-content;
    }
}

.contact__link {
    p {
        margin: 4px 0;
        font-weight: 600;
        font-size: 18px;
        a:hover {
            text-decoration: underline;
        }
    }
}

.md-block {
    max-width: 50%;
}

.block__grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(40%, 1fr));
    gap: 50px;
    margin: 12px 0px;
    &__wrapper {
        max-width: 457px;
    }
    &__item {
        display: flex;
        flex-direction: column;
        max-width: 100%;
        width: 100%;
        margin-bottom: 16px;
        align-self: center;
        position: relative;
        .img-cover {
            max-width: 100%;
            max-height: 260px;
            overflow: hidden;
            img {
                object-fit: cover;
                width: 100%;
            }
        }
        a {
            margin: 4px 0px;
            font-weight: 600;
            display: block;
            &:hover {
                text-decoration: underline;
            }
        }
    }
}

.desc-monument {
    display: inline-block;
    position: relative;
    p {
        text-align: left;
        padding-right: 4px;
        font-weight: 500;
        font-size: 18px;
    }
}

.mini-title {
    font-size: 20px;
    font-weight: 600;
}
.by-ticket_btn {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
}
.res-contact {
    display: flex;
    align-items: center;
    width: 200px;

    input {
        width: 30px;
        height: 30px;
    }

    label {
        padding: 4px;
        font-size: 12px;
    }
}

.resmail {
    display: flex;
    align-items: center;
    flex-wrap: wrap;

    button {
        padding: 8px 12px;
        border: 0;
        outline: 0;
        display: flex;
        align-items: center;
        justify-content: space-evenly;
        font-size: 16px;
        background: none;
        font-weight: 600;
        cursor: pointer;

        img {
            margin-left: 6px;
        }
    }
}

.lang.active {
    display: block;
}

@import "./slider";
@import "./select";
@import "./breakpoints";
@import "./simple-lightbox";
